import store from 'store'
import moment from 'moment'
import { update } from 'ramda'
import { Mutation, MutationOptions, ReportTemplate } from '@/constants/types'
import { useReportTemplateState } from '@/data/useReportTemplate'
import { uuid } from '@/utils/webHelper'

export const useAddNewTemplate = ({ onCompleted }: MutationOptions = {}) => {
  const [reportTemplates, setReportTemplates] = useReportTemplateState()

  const handler = (values: ReportTemplate) => {
    let id = uuid()
    let newList = [
      ...reportTemplates,
      {
        ...values,
        id,
        createdAt: moment(),
        createdAtStr: moment().format('YYYY/MM/DD HH:mm'),
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
    ]

    setReportTemplates(newList)

    store.set('reportTemplateListState', newList)

    if (onCompleted) {
      onCompleted({ ...values, id })
    }
  }

  return [false, handler] as Mutation
}

export const useUpdateTemplate = (options: MutationOptions = {}) => {
  const [reportTemplates, setReportTemplates] = useReportTemplateState()

  const handler = (values: ReportTemplate) => {
    let index = reportTemplates.findIndex(x => `${x.id}` === `${values.id}`)
    let newList = update(
      index,
      {
        ...values,
        updatedAt: moment(),
        updatedAtStr: moment().format('YYYY/MM/DD HH:mm'),
      },
      reportTemplates
    )

    setReportTemplates(newList)

    store.set('reportTemplateListState', newList)
  }

  return [false, handler] as Mutation
}
